<template>
  <div>
    <div class="tab">
          <button @click="clickTab">Search by name</button>
          <button @click="clickTab">Search by serial number</button>
    </div>
    <div class="tabcontent" v-if="tab==1">
      <input v-model="name"/>
      <button @click="searchByName">Search by name</button>
    </div>
    <div class="tabcontent" v-if="tab==2">
      <input v-model="serialNumber"/>
      <button @click="searchBySerialNumber">Search by serial number</button>
    </div>
    <table class="tabcontent" v-if="getEquipList.length > 0">
      <tr>
        <th>Name</th>
        <th>Serial number</th>
        <th>Location</th>
      </tr>
      <tr v-for="equip, index in getEquipList" :key="equip.id" @click="setCurrentEquip">
        <td>{{equip.name}}</td>
        <td>{{equip.serialNumber}}</td>
        <td>{{equip.location.name}}</td>
      </tr>
    </table>
  </div>
</template>
<script>
import {mapGetters, mapActions} from "vuex";
import axios from "axios"

  export default {
    name: 'SearchEquip',
    data() {
      return {
        tab:1,
        name:"",
        serialNumber:"",
      }
    },
    computed: mapGetters(['getEquipList']),
    methods: {
      clickTab() {
        this.$store.dispatch("updateEquipList", []);
        if(this.tab == 1) {
          this.tab = 2;
        } else {
          this.tab = 1;
        }
      },
      searchByName() {
        this.$store.dispatch("fetchEquipByName", this.name);
      },
      searchBySerialNumber() {
        this.$store.dispatch("fetchEquipBySerialNumber", this.serialNumber);
      },
      selectCurrentEquip(e) {
        this.$store.dispatch("setEquip", this.getEquipList[e.target.selectedIndex]);
        //this.currentEquip = this.equipList[e.target.selectedIndex];
      }
    },
  }
</script>
<style>
/* Style the tab */
.tab {
  overflow: hidden;
  border: 1px solid #ccc;
  background-color: #f1f1f1;
}

/* Style the buttons that are used to open the tab content */
.tab button {
  background-color: inherit;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #ddd;
}

/* Create an active/current tablink class */
.tab button.active {
  background-color: #ccc;
}

/* Style the tab content */
.tabcontent {
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-top: none;
}
</style>



